import icons from "url:../../img/icons.svg"

export default class View {

    _data
    render(data, render = true) {

        if (!data || Array.isArray(data) && data.length === 0) return this.renderError()

        this._data = data
        const template = this._generateHTML()

        if (!render) return template
        this._clear()
        this._parrentEl.insertAdjacentHTML('afterbegin', template)

    }

    update(data) {

        this._data = data
        const newTemplate = this._generateHTML()
        //Converting new template to DOM element in oder to compare to the old
        const newDom = document.createRange().createContextualFragment(newTemplate)
        const newElements = Array.from(newDom.querySelectorAll('*'))
        const curElements = Array.from(this._parrentEl.querySelectorAll('*'))

        newElements.forEach((newEl, i) => {
            const curEl = curElements[i]
            //Update changed Text
            if (!newEl.isEqualNode(curEl) && newEl.firstChild?.nodeValue.trim() !== '') {
                curEl.textContent = newEl.textContent
            }
            //Update changed attributes
            if (!newEl.isEqualNode(curEl)) {
                Array.from(newEl.attributes).forEach(attr => {
                    curEl.setAttribute(attr.name, attr.value)
                })
            }
        })
    }

    renderSpinner() {
        const markup = `
        <div class="spinner">
            <svg>
            <use href="${icons}#icon-loader"></use>
            </svg>
        </div>
        `
        this._clear()
        this._parrentEl.insertAdjacentHTML('afterbegin', markup)
    }

    renderError(message = this._errorMessage) {
        const markup = `
        <div class="error">
            <div>
                <svg>
                    <use href="${icons}#icon-alert-triangle"></use>
                </svg>
            </div>
            <p>${message}</p>
        </div>`
        this._clear()
        this._parrentEl.insertAdjacentHTML('afterbegin', markup)

    }
    renderMessage(message = this._successMessage) {
        const markup = `
        <div class="message">
            <div>
                <svg>
                    <use href="${icons}#icon-smile"></use>
                </svg>
            </div>
            <p>${message}</p>
        </div>`
        this._clear()
        this._parrentEl.insertAdjacentHTML('afterbegin', markup)

    }
    _clear() {
        this._parrentEl.innerHTML = ''
    }
}